.home-page-card {
  width: 300px;
  padding: 20px;
  border-radius: 15px;
  border: 5px solid #0c650c; /* Green border color */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
  justify-content: space-evenly;

  margin: 0 auto;
  z-index: 1;

  transform: translateY(-50%) translateX(-50%);
  position: absolute;
  top: 50%;
  left: 50%;

  /* Set initial opacity to 0 for a fade-in effect */
  opacity: 0;
  transition: opacity 1s ease-in-out;
}

.home-page-card.active {
  opacity: 1;
}
